import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_INSERT_ROW_BY_SELECTIONS = Symbol('TABLE_INSERT_ROW_BY_SELECTIONS');

export function insertTableRowBySelections(table, type) {
  return {
    type: TABLE_INSERT_ROW_BY_SELECTIONS,
    payload: {
      node: table,
      type,
    },
  };
}

interface TableInsertRowBySelectionsPayload {
  node: Table;
  type: string;
}

interface TableInsertRowBySelectionsAction {
  type: typeof TABLE_INSERT_ROW_BY_SELECTIONS;
  payload: TableInsertRowBySelectionsPayload;
}

export function isTableInsertRowBySelectionsAction(
  action: Action,
): action is TableInsertRowBySelectionsAction {
  return action.type === TABLE_INSERT_ROW_BY_SELECTIONS;
}
